//MUI
import {
  Grid,
  List,
  ListItem,
  Typography,
  Box,
  useMediaQuery,
} from "@mui/material";

import CustomLine from "../../UI/line/customLine";
import classes from "./footer.module.css";

export default function WorkedDays() {
  const matches = useMediaQuery("(min-width:768px)");

  return (
    <Grid item xs={12} sm={6} md={3} lg={3}>
      {matches ? (
        <Box style={{ userSelect: "none" }}>
          <Typography
            variant="body1"
            fontWeight="bold"
            color="textSecondary"
            align="center"
          >
            Çalışma Saatleri
          </Typography>
          <CustomLine width="28%" />
          <List sx={{ position: "absolute", marginLeft: "4.5rem" }}>
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  minWidth: "18rem",
                  borderRadius: ".5rem",
                  border: ".1rem solid black",
                }}
                className={classes.days}
                p={1.2}
              >
                <Typography
                  sx={{ opacity: ".7" }}
                >{`Pazartesi > Cuma`}</Typography>
                <Typography alignContent="flex-end" color="black" ml={3}>
                  {`10.00 > 20.00`}
                </Typography>
              </Box>
            </ListItem>
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  borderRadius: ".5rem",
                  border: ".1rem solid black",
                }}
                p={1.2}
              >
                <Typography sx={{ opacity: ".7" }}>{`Cumartesi`} </Typography>
                <Typography color="black" ml={3}>
                  {`10.00 > 20.00`}
                </Typography>
              </Box>
            </ListItem>
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  borderRadius: ".5rem",
                  border: ".1rem solid black",
                }}
                p={1.2}
              >
                <Typography sx={{ opacity: ".7" }}>{`Pazar`} </Typography>
                <Typography color="rgb(209, 83, 83)" ml={3}>
                  {`Kapalı`}
                </Typography>
              </Box>
            </ListItem>
          </List>
        </Box>
      ) : (
        <Box mt={-2}>
          <Typography
            style={{ userSelect: "none" }}
            variant="body1"
            fontWeight="bold"
            color="textSecondary"
            align="center"
          >
            Çalışma Saatleri
          </Typography>
          <CustomLine width="30%" />
          <List
            sx={{
              position: "absolute",
              display: "inline-block",
              textAlign: "center",
              left: "50%",
              transform: "translateX(-50%)",
            }}
          >
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  minWidth: "23rem",
                  borderRadius: ".5rem",
                  border: ".06rem solid black",
                }}
                className={classes.days}
                p={1}
              >
                <Typography
                  sx={{ opacity: ".7" }}
                >{`Pazartesi > Cuma`}</Typography>
                <Typography alignContent="flex-end" color="black" ml={3}>
                  {`10.00 > 20.00`}
                </Typography>
              </Box>
            </ListItem>
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  borderRadius: ".5rem",
                  border: ".06rem solid black",
                }}
                p={1}
              >
                <Typography sx={{ opacity: ".7" }}>{`Cumartesi`} </Typography>
                <Typography color="black" ml={3}>
                  {`10.00 > 20.00`}
                </Typography>
              </Box>
            </ListItem>
            <ListItem>
              <Box
                sx={{
                  width: "100%",
                  height: "100%",
                  display: "flex",
                  backgroundColor: "transparent",
                  justifyContent: "space-between",
                  borderRadius: ".5rem",
                  border: ".06rem solid black",
                }}
                p={1}
              >
                <Typography sx={{ opacity: ".7" }}>{`Pazar`} </Typography>
                <Typography color="rgb(209, 83, 83)" ml={3}>
                  {`Kapalı`}
                </Typography>
              </Box>
            </ListItem>
          </List>
        </Box>
      )}
    </Grid>
  );
}
