//Components
import AboutUs from "./aboutUs";
import Navigation from "./navigation";
import Contact from "./contact";
import WorkedDays from "./workedDays";

//MUI
import { Grid, Typography, useMediaQuery } from "@mui/material";
import { Box } from "@mui/system";

//React
import { Fragment } from "react";

import classes from "./footer.module.css";

const Footer = () => {
  const matches = useMediaQuery("(min-width:768px)");

  return (
    <Fragment>
      <Box className={classes.footer}>
        <Grid
          container
          component="footer"
          alignItems="center"
          justify="center"
          mt={2}
          spacing={2}
        >
          <AboutUs />
          <Navigation />
          <Contact />
          <WorkedDays />
        </Grid>
      </Box>
      <Box position="relative">
        <Box
          width="100%"
          height="3rem"
          sx={{
            backgroundColor: "#4B778D",
            display: "flex",
            justifyContent: "center",
            position: "absolute",
            bottom: 0,
            left: 0,
            right: 0,
          }}
        >
          {matches ? (
            <Typography
              sx={{
                userSelect: "none",
              }}
              mt={2}
              fontSize=".85rem"
              color="white"
            >
              Copyright © 2023 Psikolog Büşra Çırak. Tüm Hakları Saklıdır.
            </Typography>
          ) : (
            <Typography
              sx={{
                userSelect: "none",
              }}
              mt={2}
              fontSize=".5rem"
              color="white"
            >
              Copyright © 2023 Psikolog Büşra Çırak. Tüm Hakları Saklıdır.
            </Typography>
          )}
        </Box>
      </Box>
    </Fragment>
  );
};

export default Footer;
