import { Box, Grid, Hidden, Typography, useMediaQuery } from "@mui/material";
import CustomLine from "../../UI/line/customLine";
import logo from "../../../images/logo.png";

export default function AboutUs() {
  const matches = useMediaQuery("(min-width:768px)");

  return (
    <Grid item xs={12} sm={6} md={2} lg={2}>
      {matches ? (
        <Box>
          <Typography
            variant="body1"
            fontWeight="bold"
            color="textSecondary"
            align="center"
            style={{ userSelect: "none" }}  
          >
            Hakkımızda
          </Typography>
          <CustomLine width="28%" />
          <Box
            sx={{
              position: "absolute",
              display: "inline-block",
              left: "2rem",
              width: "15rem",
              textAlign: "center",
            }}
          >
            <img
              src={logo}
              style={{
                width: "150px",
                height: "80px",
                objectFit: "contain",
                overflow: "hidden",
                 userSelect: 'none',
              }}
              alt="logo"
            />
            <Typography
              variant="body2"
              sx={{ position: "relative", bottom: 0, left: 0, opacity: ".8" }}
              style={{ userSelect: "none" }}
            >
              Yaşam Psikolojik Danışma Merkezi olarak hedefimiz, sizin
              ihtiyaçlarınıza özel olarak hazırlanmış terapi planları ve etkili
              çözümler sunarak, sağlıklı bir zihin ve duygusal dengeye
              ulaşmanızı sağlamaktır.
            </Typography>
          </Box>
        </Box>
      ) : (
        <Hidden />
        // <Box mb={22}>
        //   <Typography
        //     variant="body1"
        //     fontWeight="bold"
        //     color="textSecondary"
        //     align="center"
        //   >
        //     Hakkımda
        //   </Typography>
        //   <CustomLine width="20%" />
        //   <Box
        //     sx={{
        //       position: "absolute",
        //       display: "inline-block",
        //       width: "20rem",
        //       textAlign: "center",
        //       left: "50%",
        //       transform: "translateX(-50%)",
        //     }}
        //   >
        //     <img
        //       src={logo}
        //       style={{
        //         width: "150px",
        //         height: "80px",
        //         objectFit: "contain",
        //         overflow: "hidden",
        //       }}
        //       alt="logo"
        //     />
        //     <Typography
        //       variant="body2"
        //       mb={2}
        //       sx={{ position: "relative", bottom: 0, left: 0, opacity: ".8" }}
        //     >
        //       Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aliquam
        //       suscipit odio lorem, in porttitor dolor vestibulum eget.
        //     </Typography>
        //   </Box>
        // </Box>
      )}
    </Grid>
  );
}
